import React from 'react';
import {BrowserRouter, Switch, Route} from 'react-router-dom';
import Header from '../Buyer/CommonFIles/Header/Header';
import Body from "../Buyer/Body/Body";
import Footer from '../Buyer/CommonFIles/Footer/Footer';
import Services from '../Buyer/Services/Services';
import ContactUs from '../Buyer/ContactUs/ContactUs';
import AllCategories from '../Buyer/AllCategories/AllCategories';
import Cart from '../Buyer/Cart/cart';
import Wishlist from '../Buyer/Wishlist/Wishlist'
import ProductDetail from '../Buyer/Productdetails/productdetail'
import Myheader from '../Buyer/Pages/App'

function PublicRoutes() {
    return (
        <BrowserRouter>
            <React.Fragment>
                <Header/>
                    <Switch>                    
                         <Route exact path={'/'} component={Body}/>
                        <Route exact path={'/Myheader'} component={Myheader}/>
                        <Route exact path={'/Wishlist'} component={Wishlist}/>
                        <Route exact path={'/Cart'} component={Cart}/>
                       
                        <Route exact path={'/all-categories/'} component={AllCategories}/>
                        <Route exact path={'/all-categories/:type'} component={() => <AllCategories />}/>
                        <Route exact path={'/services'} component={Services}/>
                        <Route exact path={'/contact-us'} component={ContactUs}/>
                        <Route exact path={'/products/:id'} component={ProductDetail}/>
                        {/* <Route exact patr={'/cart'} component={ShoppingCart}/> */}
                    </Switch>
                <Footer/>
            </React.Fragment>
        </BrowserRouter>
    );
}

export default PublicRoutes;

