import React from 'react';
import { createUseStyles, useTheme } from 'react-jss';
import { useHistory } from 'react-router-dom';
import SLUGS from '../../resources/slugs';
import {
    IconAgents,
    IconArticles,
    IconContacts,
    IconIdeas,
    IconLogout,
    IconOverview,
    IconSettings,
    IconSubscription,
    IconTickets
} from '../../assets/icons';
import { convertSlugToUrl } from '../../resources/utilities';
import LogoComponent from './LogoComponent';
import Menu from './MenuComponent';
import MenuItem from './MenuItemComponent';

const useStyles = createUseStyles({
    separator: {
        borderTop: ({ theme }) => `1px solid ${theme.color.lightGrayishBlue}`,
        marginTop: 16,
        marginBottom: 16,
        opacity: 0.06
    }
});

function SidebarComponent() {
    const { push } = useHistory();
    const theme = useTheme();
    const classes = useStyles({ theme });
    const isMobile = window.innerWidth <= 1080;

    async function logout() {
        push(SLUGS.login);
    }

    function onClick(slug, parameters = {}) {
        push(convertSlugToUrl(slug, parameters));
    }

    return (
        <Menu isMobile={isMobile}>
            <div style={{ paddingTop: 30, paddingBottom: 30 }}>
                <LogoComponent />
            </div>
            <MenuItem
                id={SLUGS.dashboard}
                title='Dashboard'
                icon={IconSubscription}
                onClick={() => onClick(SLUGS.dashboard)}
            />
            <MenuItem
                id={SLUGS.catalog}
                items={[SLUGS.categories, SLUGS.products, SLUGS.info]}
                title='Catalog'
                icon={IconOverview}
            >
                <MenuItem
                    id={SLUGS.categories}
                    title='Categories'
                    level={2}
                    icon={IconAgents}
                    onClick={() => onClick(SLUGS.categories)}
                />
                <MenuItem
                    id={SLUGS.products}
                    title='Products'
                    level={2}
                    icon={IconContacts}
                    onClick={() => onClick(SLUGS.products)}
                />
                <MenuItem
                    id={SLUGS.info}
                    title='Information'
                    level={2}
                    icon={IconArticles}
                    onClick={() => onClick(SLUGS.info)}
                />
            </MenuItem>
            <MenuItem
                id={SLUGS.sales}
                title='Sales'
                icon={IconTickets}
                onClick={() => onClick(SLUGS.sales)}
            />
            <MenuItem
                id={SLUGS.sellers}
                title='Sellers'
                icon={IconContacts}
                onClick={() => onClick(SLUGS.sellers)}
            />
            <MenuItem
                id={SLUGS.customers}
                title='Customers'
                icon={IconAgents}
                onClick={() => onClick(SLUGS.customers)}
            />
            <MenuItem
                id={SLUGS.reports}
                title='Reports'
                icon={IconArticles}
                onClick={() => onClick(SLUGS.reports)}
            />
            {/* <div className={classes.separator}></div>
            <MenuItem
                id={SLUGS.settings}
                title='Settings'
                icon={IconSettings}
                onClick={() => onClick(SLUGS.settings)}
            />

            <MenuItem id='logout' title='Logout' icon={IconLogout} onClick={logout} /> */}
        </Menu>
    );
}

export default SidebarComponent;
